header {
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
    position: sticky;
    top: 0px;
    background: white;
  }

  img {
    height: 10vh;
    width: 10vh;
  }
  
  nav {
    min-height: 10vh;
    margin: auto;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  nav h1 {
    max-width: 30vh;
  }
  
  nav h1,
  nav ul {
    font-size: 1.5rem;
    flex: 1;
  }
  
  nav ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
  }
  
  nav .button {
    color: black;
    text-decoration: none;
    padding-bottom: 15px;
    padding-left: 5px;
    padding-right: 5px;
  }
  
  section {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  section h2 {
    font-size:5rem;
    color:white;
  }
  
  
  
  .projects {
    background: linear-gradient(to left, #E100FF, #7F00FF);
  }
  
  .contact {
    background: linear-gradient(to right, #b5e758, #45B649);
  }
  
  .bubble {
    position: absolute;
    z-index: -2;
    background: linear-gradient(to left, #06d9e0, #2948ff);
  }

  @media all and (max-width:800px)
  {
      nav, ul
      {
          display: block; 
          float: none; 
          width: 80%;
      }
  }